import {Util} from '../commontypes/Util';
import {Unit} from '../REST';

/**
 * @class MeasureParameters
 * @deprecatedclass SuperMap.MeasureParameters
 * @category iServer Map Measure
 * @classdesc 
 * @param {GeoJSONObject} geometry - 
 * @param {Object} options - 
 * @param {Unit} [options.unit=Unit.METER] - 
 * @param {string} [options.prjCoordSys] - 
 * @param {string} [options.distanceMode="Geodesic"] -  'Geodesic'  'Planar' 
 * @usage
 */
export class MeasureParameters {



    constructor(geometry, options) {
        if (!geometry) {
            return;
        }
        /**
         * @member {GeoJSONObject} MeasureParameters.prototype.geometry
         * @description <br>
         * {@link GeometryPoint}|{@link L.Marker}|{@link L.CircleMarker}|{@link L.Circle}|{@link L.GeoJSON}|{@link ol.geom.Point}|{@link ol.format.GeoJSON}|{@link GeoJSONObject}<br>
         * {@link GeometryLineString}|{@link GeometryLinearRing}|{@link L.Polyline}|{@link L.GeoJSON}|{@link ol.geom.LineString}|{@link ol.format.GeoJSON}|{@link GeoJSONObject}<br>
         * {@link GeometryPolygon}|{@link L.Polygon}|{@link L.GeoJSON}|{@link ol.geom.Polygon}|{@link ol.format.GeoJSON}|{@link GeoJSONObject}
         */
        this.geometry = geometry;

        /**
         * @member {Unit} [MeasureParameters.prototype.unit=Unit.METER]
         * @description 
         */
        this.unit = Unit.METER;

        /**
         * @member {string} [MeasureParameters.prototype.prjCoordSys]
         * @description 
         */
        this.prjCoordSys = null;

        /**
         * @member {string} [MeasureParameters.prototype.distanceMode="Geodesic"]
         * @description  'Geodesic'  'Planar' 
         * @example
         * var param = new MeasureParameters(getmetry,{distanceMode:'Planar'});
         */
        this.distanceMode = null;
        if (options) {
            Util.extend(this, options);
        }
        this.CLASS_NAME = "SuperMap.MeasureParameters";
    }

    /**
     * @function MeasureParameters.prototype.destroy
     * @description 
     */
    destroy() {
        var me = this;
        me.geometry = null;
        me.unit = null;
        me.prjCoordSys = null;
    }
}
